<template>
  <div id="useSituation">
    <div class="body">
      <div class="title">
        <span>2<i style="opacity: .5;">/4</i></span>
        <span>
          <van-icon name="records-o" size="22" color="#1989fa;"/>&nbsp;肢体使用情况
        </span>
      </div>
      <van-divider />
      <form>
        <div class="one">
          <h3>
            <span class="iconfont icon-bitian" style="color: #ec6661;"></span>
            <i style="opacity: .5;">[单选题]</i>
            请问您目前是否正在使用假肢
            <van-tag type="warning" v-if="message[0].bool">您未完成此项</van-tag>
          </h3>
          <van-radio-group v-model="message[0].data" direction="horizontal" class="one-radio-group">
            <van-radio name="1">是</van-radio>
            <van-radio name="2">否</van-radio>
          </van-radio-group>
        </div>
        <div class="two">
          <h3>
            <span class="iconfont icon-bitian" style="color: #ec6661;"></span>
            <i style="opacity: .5;">[单选题]</i>
            请问您使用假肢的时间有多长?
            <van-tag type="warning" v-if="message[1].bool">您未完成此项</van-tag>
          </h3>
          <input type="text" placeholder="请输入您使用假肢的时间" v-model="message[1].data">
        </div>
        <div class="three">
          <h3>
            <span class="iconfont icon-bitian" style="color: #ec6661;"></span>
            <i style="opacity: .5;">[多选题]</i>
            请问您穿戴假肢的目的是什么?
            <van-tag type="warning" v-if="message[2].bool">您未完成此项</van-tag>
          </h3>
          <van-checkbox-group v-model="message[2].data">
            <van-checkbox name="1" shape="square">改善外观</van-checkbox>
            <van-checkbox name="2" shape="square">恢复自理能力</van-checkbox>
            <van-checkbox name="3" shape="square">提高运动能力</van-checkbox>
            <van-checkbox name="4" shape="square">增加工作机会</van-checkbox>
            <van-checkbox name="5" shape="square">提高生活质量</van-checkbox>
            <van-checkbox name="6" shape="square">促进社交活动</van-checkbox>
            <van-checkbox name="7" shape="square">减轻心理负担</van-checkbox>
          </van-checkbox-group>
        </div>
        <div class="four">
          <h3>
            <span class="iconfont icon-bitian" style="color: #ec6661;"></span>
            <i style="opacity: .5;">[单选题]</i>
            您对目前使用的假肢满意度如何
            <van-tag type="warning" v-if="message[3].bool">您未完成此项</van-tag>
          </h3>
          <van-radio-group v-model="message[3].data" class="four-radio-group">
            <van-radio name="1">非常满意</van-radio>
            <van-radio name="2">比较满意</van-radio>
            <van-radio name="3">一般</van-radio>
            <van-radio name="4">不太满意</van-radio>
            <van-radio name="5">非常不满意</van-radio>
          </van-radio-group>
        </div>
      </form>
    </div>
    <div class="tapBar">
      <div class="tapBarBox" style="border-right: 1px solid rgba(0, 0, 0,.2);color: #1989fa;">
          <button @click="$router.back()">上一页</button>
        </div>
      <div class="tapBarBox">
        <button :disabled="isdisabled" @click="skip" type="buttom">下一页</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      message: [
        { data: '', bool: false },
        { data: '', bool: false },
        { data: [], bool: false },
        { data: '', bool: false }
      ],
      isSkip: [],
      isdisabled: false,
      timer: null
    }
  },
  methods: {
    skip () {
      this.isSkip = JSON.parse(localStorage.getItem('useSituation'))
      if (this.isSkip === null) {
        this.message.forEach(item => {
          item.bool = true
        })
        this.isdisabled = true
        this.timer = setTimeout(() => {
          this.message.forEach(item => {
            item.bool = false
          })
          this.isdisabled = false
        }, 2000)
        return
      }

      if (this.message.find((item) => item.data.length === 0)) {
        this.isdisabled = true
        this.message.forEach(item => {
          if (item.data.length === 0) {
            item.bool = true
          }
        })

        this.timer = setTimeout(() => {
          this.message.forEach(item => {
            item.bool = false
          })
          this.isdisabled = false
        }, 2000)

        return
      }

      this.$router.push('/survey/demand')
    }
  },
  created () {
    if (localStorage.getItem('useSituation')) this.message = JSON.parse(localStorage.getItem('useSituation'))
  },
  watch: {
    message: {
      deep: true,
      handler (newVal) {
        localStorage.setItem('useSituation', JSON.stringify(newVal))
      }
    }
  }
}
</script>

<style scoped>
.one-radio-group{
  display: flex;
  justify-content: space-around;
}
  form .one,
  form .two,
  form .three,
  form .four{
  margin-bottom: 15px;
}
  form .two input{
  margin: 0 20px;
  padding: 5px 10px;
  width: 300px;
  border: 1px solid rgba(0, 0, 0,.8);
  outline: none;
  color: rgba(0, 0, 0,.8);
}
</style>
