<template>
  <div id="survey">
    <van-nav-bar
      title="调查问卷"
      left-text="退出"
      left-arrow
      @click-left="returnBack"
    />
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'surveyPage',
  data () {
    return {
      timer: null
    }
  },
  methods: {
    returnBack () {
      this.$dialog.confirm({
        message: '是否退出'
      }).then(() => {
        this.$toast.success('保存成功')
        this.timer = setTimeout(() => {
          this.$router.push('/frame/home')
        }, 2000)
      }).catch(() => {})
    }
  }
}
</script>

<style scoped>
  .body{
    padding: 5px;
  }
</style>
